#about-content {
    /* width: 75%; */
    margin: 50px 200px;
    
}

.about-section {
    display: grid;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    grid-gap: 50px;
    gap: 50px;
}

#why-frontend {
    grid-template-columns: -webkit-min-content 1fr;
    grid-template-columns: min-content 1fr;
}

#next-step {
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
}

#why-frontend-answer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.subtitle {
    font-size: xx-large;
    font-weight: bold;
}

#first-word-Why {
    color: rgb(185, 24, 24);
}

#first-word-Next {
    color: rgb(17, 74, 158);
}

.about-text {
    font-size: larger;
    line-height: 2;
    font-family: 'Roboto Mono',
    monospace;
}
#myselfImg {
    border-radius: 75px;
    height: 600px;
}

#content {
    margin: 100px;
    display: grid;
    grid-template-columns: 1fr minmax(-webkit-min-content, 1fr);
    grid-template-columns: 1fr minmax(min-content, 1fr);
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    float: center;
    grid-gap: 60px;
    gap: 60px;
    justify-items: center;
}

#myselfImg {
    grid-column: 1/2;
}

#myInfo {
    grid-column: 2/3;
    float: center;
}

#buttons {
    grid-column: 2/3;
    display: grid;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    grid-gap: 100px;
    gap: 100px;
    float: center;
    justify-items: center;
    padding-top: 150px;
}

#myPara {
    padding-top: 50px;
}

#myPara, #resume {
    grid-column: 1/-1;
}

#linkedIn, #github {
    height: 120px;
}

#resume {
    border-radius: 75px;
    height: 55px;
    width: 100%;
    cursor: pointer;
}

#resume:hover {
    background-color: white;
}
.external {
    text-decoration: none;
    color: inherit;
}

.description:hover>.company-info .external::after,
.description.hover>.company-info .external::after {
    content: "\e89e";
    font-size: 20px;
    color: rgba(30, 30, 214, 0.815);
    position: relative;
    top: 4px;
}

.hover-me {
    float: right;
    padding-right: 250px;
    padding-top: 20px;
    font-size: xx-large;
    color: rgba(119, 119, 119, 0.596);
    font-style: oblique;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}

.description:hover>.hover-me,
.description.hover>.hover-me {
    display: none;
}

.description {
    border: 1px solid rgb(41, 28, 116);
    border-radius: 75px;
    padding: 30px 30px 30px 50px;
    width: 2fr;
    box-shadow: 10px 5px 5px rgba(204, 25, 25, 0.705);
    overflow: hidden;
}

.description:hover,
.description.hover {
    box-shadow: 10px 5px 5px rgba(204, 141, 25, 0.397);
    /* transform: scale(1, 1.5);
    transition: 0.6s;
    transform-style: preserve-3d;

    position: relative; */
}

.title,
.company-info {
    font-size: xx-large;
}

.description>.title {
    font-weight: bold;
    margin-bottom: 10px;
    font-family: 'Chango', cursive;
    font-family: 'Fjalla One', sans-serif;
}

.job-description {
    display: none;
    line-height: 1.6;
    font-family: 'Roboto Mono',
    monospace;
}

.description:hover>.job-description,
.description:hover>.title,
.description:hover>.company-info,
.description.hover>.job-description,
.description.hover>.title,
.description.hover>.company-info {
    font-size: medium;
}

.description:hover>.job-description,
.description.hover>.job-description {
    display: inline;
    display: initial;
}


.experience {
    display: grid;
    grid-template-rows: -webkit-min-content;
    grid-template-rows: min-content;
    grid-auto-flow: dense;
    margin: 100px;
}

.experience:nth-child(odd) {
    grid-template-columns: 2fr 1fr;
}

.experience:nth-child(even) {
    grid-template-columns: 1fr 2fr;
}

.experience>.description {
    grid-column: 1;
}

.experience>.time {
    grid-column: 2;
}

.experience:nth-child(even)>.description {
    grid-column: 2;
}

.experience:nth-child(even)>.time {
    grid-column: 1;
}

.experience>.time {
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    font-size: xx-large;
    font-family: 'Dancing Script',
    cursive;
}

.description>.company-info {
    margin-bottom: 20px;
}

header {
    box-sizing: border-box;
    position: fixed;
    top: 0;
    height: 75px;
    width: 100%;
    background-color: #1e6f5c;
    color: white;
    font-size: 20px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

#NameAndIcon {
    display: -webkit-flex;
    display: flex;
    position: relative;
    float: left;
    height: inherit;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 35px;
    font-family: 'Indie Flower',
    cursive;
}

#nav {
    display: grid;
    grid-template-columns: repeat(4, -webkit-min-content);
    grid-template-columns: repeat(4, min-content);
    padding-right: 40px;
    position: relative;
    height: inherit;
    float: right;
    grid-gap: 3em;
    gap: 3em;
    -webkit-align-items: center;
            align-items: center;
}

#nav>a {
    display: -webkit-flex;
    display: flex;
    padding: 20px 10px;
    text-decoration: none;
    color: inherit;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#nav>.nav-item:hover {
    border-bottom: 4px dotted rgba(51, 51, 204, 0.692);
    background-color: #ffffff1c;
}

body {
    padding-top: 65px;
    font-family: Arial;
    margin: 0;
}

.active {
    background-color: #ffffff1c;
}
#projects-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: minmax(480px, 500px);
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    justify-items: center;
    margin: auto 80px 50px 80px;

    margin-top: 10%;
}

.external::after {
    content: "\e89e";
    font-size: 20px;
    color: rgba(30, 30, 214, 0.815);
    position: relative;
    top: 4px;
}

.project-description {
    padding: 15px;
    text-align: justify;
    line-height: 1.6;
    font-size: larger;
}

.project-image {
    margin: 0 30px;
    margin-top: 100px;

}

.project-title {
    margin-top: 50px;
    text-align: center;
    font-size: xx-large;
    font-family: 'New Tegomin',
    serif;
    font-weight: bold;
}

.flip-container {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    border: 1px solid rgb(41, 28, 116);
}

.flip-container:hover .flipper,
.flip-container.hover .flipper {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.flip-container,
.front,
.back {
    border-radius: 75px;
    width: 320px;
    height: 480px;
}

.flipper {
    transition: 0.8s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
}

.front,
.back {
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 100%;
}

.front {
    z-index: 2;
}

.front {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

.back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

